// SET OUTER MARGINS ON SECTION
@mixin outerMargins() {
  padding-left: $outer-margin * 0.5;
  padding-right: $outer-margin * 0.5;

  @include mq($from: tablet) {
    padding-left: $outer-margin;
    padding-right: $outer-margin;
  }
}

@mixin horMargins() {
  padding-top: $h-space * 0.7;
  padding-bottom: $h-space * 0.8;

  @include mq($from: tablet) {
    padding-bottom: $h-space;
    padding-top: $h-space;
  }
}


//kanske ta bort
@mixin horMarginBottom() {
  padding-bottom: $h-space * 0.8;

  @include mq($from: tablet) {
    padding-bottom: $h-space;
  }
}
