body.home {

//VARS
$h-space: $h-space * 0.8;
$small-h-space: $h-space * 0.5;
$top-offset: 176px;

.right-wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.header-spacer {
  display: none;
  @include mq($from:desktop) {
    display: block;
    height: $top-offset;
    position: fixed;
    width: 70.4%; //FULT
    z-index: 22;
    background: linear-gradient(to top, rgba(255, 255, 255, 0), #ffffff);
    opacity: 0.9;
  }
}

p.intro, p {
  padding-bottom: 0; //FUL PLATS
  margin-bottom: 0; //FUL PLATS
}

#logo-narrow {
  @include mq($from:desktop) {
    display: none;
  }
}

#logo {
  display: none;
  @include mq($from:desktop) {
    display: block;
  }
}

#menu {
  @include mq($from:desktop) {
    padding-top: 174px;
  }
}

section.text-module {
  p {
    margin: 0;
  }

  &.two {
    width: 47.5%;

    &.first {
      margin-right: $gutter;
    }
  }
}

section.img-module {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  .wrapper {
    width: 100%;
    @include mq($from:tablet) {
      width: 33%;
      position: relative;
    }
  }

  article {
    display: flex;
    height: 100%;
    width: 100%;
    padding-right: 5%;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    z-index: 20;

    @include mq($from:tablet) {
      position: absolute;
    }

    h1 {
      @include mq($from:tablet) {
        width: 170%;
      }
    }
  }

  figure {
      width: 100%;
      position: relative;
      z-index:-1;
      align-self: flex-end;
      overflow: hidden;

      @include mq($from:tablet) {
        width: 67%;
      }

      div.cover {
          display: block;
          width: 50%;
          position: absolute;
          z-index: 10;
          background: $gradient-left;
          opacity: $opactity;
          left: 0;
          top: 0px;
          height: 101%;
          mix-blend-mode: screen;
      }

      img {
        z-index: 0;
      }
  }

  &.one, &.half {
    @include mq($until:tablet) {
      figure {
        margin-top: $h-space * .8;
      }
    }
  }


  &.half {
    @include mq($from:tablet) {

    .wrapper, figure {
      width: 50%;
    }
    h1 {
      width: 100%;
    }
  }
  }

  &.two {
    width: 100%;

    @include mq($from:tablet) {
      width: 47.5%;
    }

    &.first {
      @include mq($from:tablet) {
        margin-right: $gutter;
      }
    }

    figure {
      @include mq($until:tablet) {
        width: 50%;
        margin-left: 50%;
        margin-top: -1.5em;

        .cover {
          background: $gradient-top;
          width: 100%;
        }

      }
    }

  }

}

span.arrow {
  color: white;
  margin-left: -16px;
  width: 16px;
  display: inline-block;
  font-family: $body-font-bold;
}

article a:hover, figure {
  span.arrow {
    color: black;
  }
}

.border {
  margin-top: $h-space;
  margin-bottom: $h-space;
  height: 4px;
  background: black;
  width: 100%;

  &.line1 {
    height: 4px;
  }
  &.line2 {
    height: 6px;
  }
  &.line3 {
    height: 1px;
  }
  &.line4 {
    height: 1px;
  }
  &:first-of-type {
    margin-top: 0;
    @include mq($from:desktop) {
      margin-top: $top-offset;
    }
  }
  &.second {
    @include mq($from:tablet) {
      display: none;
    }
  }

}

}
