
.thumb {
    margin-right: $gutter;
    padding-bottom: 1.5em;
    position: relative;

  @include mq($until:tablet) {
    width: 47.5%;
    @include resetLastColumn(2);
  }

  @include mq($from:tablet,$until:desktop) {
    width: 30%;
    @include resetLastColumn(3);

  }
  @include mq($from:desktop) {
    width: 21.25%;
    @include resetLastColumn(4);
  }

  @include mq($from:tablet) {
    padding-bottom: 2.5em;
  }

  &:not(.year):hover {
    border-bottom: 0px;

    .cover {
      opacity: 0;
    }

    .arrow {
      color: black;
    }

    .year {
      z-index: -2;
    }
  }

  span.year {
    display: none;
    @include mq($from:tablet) {
      display: block;
      position: absolute;
      top: -10px;
      left: -20px;
      font-size: 80px;
      font-family: "bac03";
      z-index: 100;
      pointer-events: none;
    }
  }

  .cover {
    width: 100%;
    position: absolute;
    z-index: 10;
    background: linear-gradient(to top , rgba(255, 220, 180, 0), #ffdcb4);
    opacity: $opactity;
    left: 0;
    top: 0px;
    height: 70%;
    mix-blend-mode: screen;
    transition: opacity .25s ease-out;
    }

  .img {
    position: relative;
    line-height: 0em;
    display: block;

  }

  h2 {

    span.arrow {
      color: white;
      margin-left: -16px;
      width: 16px;
      display: inline-block;
      font-family: $body-font-bold;
    }

  }

}

.thumb-container {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 1em;
}
