/*********************
GENERAL STYLES
*********************/

body {
	font-size: 100%;
  width: 100%;
	line-height: 1.4em;
	color: $black;
	background-color: white;
	font-family: $body-font;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
}

html {
  box-sizing: border-box;
}

::-webkit-scrollbar {
    -webkit-appearance: none;
    background: #fff
}

::-webkit-scrollbar:vertical {
    width: 10px
}

::-webkit-scrollbar:horizontal {
    height: 10px
}

::-webkit-scrollbar-thumb {
    background: #000;
}


*, *:before, *:after {
  box-sizing: inherit;
}

p {
margin-top: 0;
}

h1, h2, h3, h4, h5 {
	margin: 0;
}

/* Images */

img {
	max-width: 100%;
	width: auto;
}

.entry-content img,
.comment-content img,
.widget img {
    max-width: 97.5%; /* Fluid images for posts, comments, and widgets */
}
img[class*="align"],
img[class*="wp-image-"] {
    height: auto; /* Make sure images with WordPress-added height and width >attributes are scaled correctly */
}
img.size-full {
    max-width: 100%;
    width: auto; /* Prevent stretching of full-size images with height and >>width attributes in IE8 */
}

#wrap {
  padding-top: 70px;
}

/*********************
LINK STYLES
*********************/

p a {
	border-bottom: 1px solid black;
	}

div a{
	text-decoration: none;
	color: black;
	}

a {
	text-decoration: none;

	&:hover {
	color: black;
	border-bottom: 1px solid black;
	}
	&:visited {
		color:	black;
		}
}

#logo a:hover {
	border-bottom: 0px;
}

/******************************************************************
FRAMEWORK
******************************************************************/

#content {
	padding-left: $outer-margin;
  padding-right: $outer-margin;
  padding-bottom: $v-space;
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;

	@include mq($from: wide) {
		max-width: 1400px;
		margin: auto;
	}

	.left-wrapper {
		width: 100%;

		@include mq($from:desktop) {
			display: block;
			width: 20%;
			.left {
				position: fixed;
			}
		}
	}

	.right-wrapper {
		width: 100%;
		opacity: 0;

		@include mq($from:desktop) {
			width: 80%;
		}

		.right {
			width: 100%;

			@include mq($from:desktop) {
				width: 75%;
			}
		}

		.text-container {
			display: flex;
			flex-wrap: wrap;

			.main-text {
				width: 100%;
				@include mq($from:tablet) {
					width: 70%;
					padding-right: 5%;
				}
			}

			.meta-text {
				width: 100%;
				@include mq($from:tablet) {
					width: 25%;
				}

				#caption {
					display: none;
					@include mq($from:desktop) {
					display: block;
					padding-bottom: 1em;
					}
				}

			}

			#wpgmza_map {
				margin-bottom: $h-space;
			}

			.back-to-overview {
				margin-top: 2em;

				@include mq($until:desktop) {
					margin-bottom: 1em;
				}
			}

		}

		.img-container {
			margin-bottom: $v-space;
			text-align: center;
			line-height: 0;

			&.borders {
				border-left: 4px solid black;
				border-right: 1px solid black;
			}

			img {
				max-height: calc(100vh - 225px);
			}

			#caption {
				padding-top: 1em;
				line-height: 1.4em;
				text-align: left;
				@include mq($from:desktop) {
					display: none;

				}
			}

		}

	}
}




///// EXTRA


.mobile-hide {
	@include mq($from: mobile, $until: tablet) {
		display: none;
	}
}
