// FONT EMBEDDING & TYPO VARIABLES
$ff-font: "bac01";
$ff-font-outline: "FF Gothic Outline Regular";
$body-font: "StyreneB-Regular";
$body-font-italic: "StyreneB-RegularItalic";
$body-font-bold: "StyreneA-Black";
$body-font-medium: "StyreneA-Bold-Web";

// Ta bort över tid
$sans-serif:        Helvetica, Arial, sans-serif;
$serif:             "Georgia", Cambria, Times New Roman, Times, serif;
$lato:              'Lato', $sans-serif;

@font-face {
    font-family: 'bac01';
    src: url('../fonts/bac-01-webfont.eot');
    src: url('../fonts/bac-01-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/bac-01-webfont.woff') format('woff'),
         url('../fonts/bac-01-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}


