body.page-template-page-contact {

  .text-container {
    justify-content: space-between;

    p, form {
      width: 70%;

      @include mq($from: tablet) {
        width: 47.5%;
      }
    }

    p:first-of-type {
      width: 70%;
    }

    .border {
      margin-top: $h-space;
      margin-bottom: $h-space;
      width: 100%;
      height: 3px;
      background: black;
    }

  }




  #newsletter {

  table {
  	width: 100%;
  	}

  td.first {
  	width: 80px;
  	vertical-align: middle;
  	}

  td a {
  	padding-top: 1em;
  	padding-top: 1em;
  	display: block;

  	&:hover {
  		border-bottom: 0px;
  	}
  }

  }

}
