
$logo-size: 80px;
$intro-size:      18px;
$body-size:       16px;
$mid-size:        16px;
$small-size:      14px;
$very-small-size: 12px;
$headline-size:   22px;
$icon-size:       35px;

$line-height:     1.45em;


h1 {
  font-size: $headline-size;
  font-family: $body-font-medium;
  @include media('retina2x') {
      font-family: $body-font-bold;
      letter-spacing: -1px;
  }

  line-height: 1.2em;

  body.home & {
    padding-bottom: 0.5em;
  }

  &.big {
    font-size: $headline-size + 4px;
  }

  &.very-big {
    font-size: $headline-size + 8px;
  }

}


h2 {
  font-weight: normal;
  font-size: $body-size;
  padding: 0px;
  margin: 0px;
  padding-bottom: 1em;
  text-transform: uppercase;
  letter-spacing: 1px;
}

body.category h2, body.page-template-page-projects-artist h2 {
  text-transform:none;
  letter-spacing:0px;
  padding-bottom:0em;
  font-family: $body-font-medium;
  padding-top: 0.8em;
  line-height: 1.2;
  font-size: $body-size;
  @include media('retina2x') {
      font-family: $body-font-bold;
  }
}

body.home h2 {
  font-size:1em;
  padding-bottom:0px;
  font-family: $body-font-medium;
  letter-spacing:0px;
  text-transform:none;
  margin-bottom:0em;
  @include media('retina2x') {
      font-family: $body-font-bold;
  }
}

h3 {
  font-weight: normal;
  font-size: $body-size;
  padding:0px;
  margin:0px;
  line-height: 1.4;
  padding-top: 2px;
}

body.home h3 {
  font-size: $small-size;
  text-transform: uppercase;
  padding-bottom: 0.5em;
  //letter-spacing: 1px;
}

/* Används för subtitle på single */
h4 {
  font-size: 1.1em;
  font-weight:normal;
  margin:0px;
  padding-bottom: 1.5em;
  letter-spacing: 1px;
  text-transform:uppercase;
  line-height: 1.5em;
}

body.category-projects-title h3 {
  padding-bottom:1em;
}

strong {
  font-weight:normal;
  font-family: $body-font-medium;
  @include media('retina2x') {
      font-family: $body-font-bold;
  }
}

p strong {
  display:inline-block;
}

em {
  font-style:normal;
  font-family: $body-font-italic;
}

p {
  line-height: 1.5em;
}

.intro p, p.intro, body.page #text-container p {
  font-size: $intro-size;
  padding-bottom: 0.5em;
}

.meta-text {
  font-size: $small-size;
  line-height: 1.4em //snygga upp;
}

p.cv  {
  font-size: $mid-size;
  line-height: 1.4em //snygga upp;
}
  p.cv span {
    text-transform:uppercase;
    letter-spacing:1px;
    font-size: $very-small-size;
    font-family: $body-font-bold;

  }

#meta p, #caption {
  font-size: $small-size;

  strong {
  letter-spacing: 1px;
  font-family: $body-font-bold;
  text-transform: uppercase;
  font-size: $very-small-size;
  padding-top: 1em;
  }
}

.two-columns p {
  margin-top:0.6em;
}
