
// MARGINS & SPACES
$outer-margin: 6%;
$v-space: 40px;
$h-space: $v-space;

$opactity: 0.7;


// GRID
$grid-gutter: 3%;
$columns: 8;
$gutter: 5%;
$column-width: ((100% - (($columns - 1) * $gutter)) / $columns);

//LINEs
$separator: 5px solid black;
$separator-white: 4px solid white;


// COLOR
//$brandcolor:        #15d9fc;
$brandcolor:        wheat;
$gradient-left:     linear-gradient(to left, rgba(255,220,180,0), rgba(255,220,180,1));
$gradient-top:      linear-gradient(to top, rgba(255,220,180,0), rgba(255,220,180,1));


$red:               #e13000;
$blue:              #0072bc;
$green:             #349f49;

$light-gray:        #f5f5f5;
$mid-gray:          #707070;
$dark-gray:         #424242;


$black:             #000000;


