#menu {

  display: none;

  @include mq($from:mobile, $until: desktop) {
    background: black;
    line-height: 2.5;

    a {
      color: white;
      width: 100%;
      padding: 10px;
      border-top: 1px solid white;
      display: block;

      &:hover {
        border-bottom: 0px solid white;
      }
    }

    ul.sub-menu {
      padding-left: 0px !important;

      a {
        padding-left: 20px;
      }

    }

  }


  @include mq($from:desktop) {
    display: block;
  }

  line-height: 1.6em;
  opacity: 0;

  ul {
    list-style-type: none;
    padding-left: 0px;
  }

  ul.sub-menu
  {
  list-style-type: none;
    padding-left: 1em;
    margin-top: 0px;
    margin-bottom:  0px;
  }

  .current-menu-item a, {
    border-bottom: 1px solid black;
  }

  .lang-item {
    @include mq($from:desktop) {
      padding-top: .5em;
    }
  }
}

.left #menu {
  @include mq($until:desktop) {
    margin-bottom: $h-space;
  }
}

body.projects .current-menu-parent a, body.events .current-menu-parent a, body.evenemang .current-menu-parent a, body.projekt .current-menu-parent a, {
  border-bottom: 1px solid black;
}
