#top-line {
	position:fixed;
	top:0px;
	width:100%;
	border-top: 10px solid black;
	z-index: 1000;
	}

.loader {
  position: fixed;
  bottom: 0;
  margin-bottom: $h-space;
  right: $outer-margin;
  margin-right: -6px;

  span {
    font-size: $logo-size;

		&.desktop {
			display: none;
			@include mq($from:tablet) {
				display: inline;
			}
		}

		&.mobile {
			@include mq($from:tablet) {
				display: none;
			}
		}
  }
}

#logo, #logo-narrow {
  font-size: $logo-size;
  line-height: 1;
  position: relative;
  margin-left: -6px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: $h-space * 0.6;
	padding-bottom: $h-space * .5;

			@include mq($from:tablet) {
				padding-top: $h-space * 1.2;
				padding-bottom: $h-space * 1;
			}

  span.bac01 {
    font-family: "bac01";
  }

  span.bac02 {
    font-family: "bac02";
  }

  span.bac03 {
    font-family: "bac03";
  }

  span.bac04 {
    font-family: "bac04";
  }

  #hamburger {
    display: none;
  }

	a.mobile-menu {
		font-size: $body-size;
		@include mq($from:desktop) {
			display: none;
		}
	}

	a:hover {
		border-bottom: 0px;

	}

}

#header{
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
  position: relative;
	padding-bottom: $h-space * 0.7;

	@include mq($from: tablet) {
		padding-bottom:    $h-space * 1.7;
	}

	@include mq($from: desktop) {
		padding-top:    $h-space * 2;
	}

  h1 {
    z-index: 21;
  }

  }

body.category-events {
  #header div {
    display: none;
  }
}

#header-start {
	display: none;
	@include mq($from:desktop) {
		display: block;
  width: 100%;
  float: left;
  position: fixed;
  height: 100px;
  z-index: 2000;
  }
}
