footer {
  display: flex;
  width: 100%;
  padding-top: 2em;
  opacity: 0;

  #up {
    display: none;

    @include mq($from: desktop) {
      width: 20%;
      display: block;
    }
  }

  div + div {
    width: 80%;
  }
}

#bottom-line {
	position: fixed;
	bottom: 0px;
	width: 100%;
	border-top: 4px solid black;
	z-index: 1000;
}
